*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.app-header{
  width: 100%;
  height: 90px;
  background-color: hsla(205, 45%, 43%, 0.849);
}

.app-header h1{
  text-align: center;
  padding: 2rem 0;
  font-size: 2em;
  color: white;
}

#cards h2{
  text-align: center;
  font-size: 2em;
}

#cards h3{
  text-align: center;
  font-size: 4em;
  padding: 1rem 3rem;

}

p{
  text-align: center;
  font-size: 1em;
  padding: 1rem 0;
}

#cards{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90%;
  margin: 2rem auto;
}

.card-left , .card-middle , .card-right {
  margin: 1rem;
  border-radius: 10px;
  padding: 1.3rem;
  width: 80%;
  background-color: white;
  box-shadow: 0px 0px 19px -6px rgba(0, 0, 0, 0.75);

}

#cards hr{
  width: 100%;
  margin: auto;
  opacity: 0.3;
}

.card-left , .card-right {
  background-color: white;
  border-bottom-right-radius: 10px;
  border-top-left-radius: 10px;
}

.card-middle {
  padding: 3rem 1.3rem;
  border-radius: 10px;
  background:linear-gradient(rgb(44, 121, 192), hsl(192, 59%, 54%));
  color: white;
  border-radius: 10px;
} 

.card-middle p , .card-left p , .card-right{
  padding: 1rem 0;
}

.card-left p, .card-right p{
  color: rgba(128, 128, 128, 0.863);
}

.card-left{
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
}

.card-right{
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}

button {
  cursor: pointer;
  display: flex;
  box-shadow: 5px 5px 25px 1px rgba(0, 0, 0, 0.25);
  outline: 0;
  justify-content: center;
  font-size: 1rem;
  padding: 0.8rem;
  margin: 1.5rem auto 0.5rem auto;
  transition: 0.3s linear;
  color: white;
  background-color: black;
  width: 85%;
  border: none;
  border-radius: 6px;
  text-transform: uppercase;
  transition: transform 0.7s;
}

button:hover{
  transform: scale(1.05);

} 

button:active {
  transform: scale(0.85);
  box-shadow: 3px 2px 25px 1px rgba(0, 0, 0, 0.25);
}

@media (max-width: 976px){
    #cards{
      flex-direction: column;
      width: 70%;
    }

    h3{
      font-size: 1.5em;
    }
}
